/* TABLE */
.table {
    padding: 10px;
    margin: 10px auto;
}
.table--collapse {
    border-collapse: collapse;
}
.table--small {
    font-size: 14px;
}
.table--striped tbody tr:nth-child(odd) {
    background: #fafafa;
}
.table th, .table td {
    padding: 5px;
    vertical-align: top;
}
/* ALIGNMENT */
.text-right {
    text-align: right;
}
.text-center {
    text-align: center;
}
.text-small {
    font-size: 14px;
}
.no-wrap {
    white-space: nowrap;
}

/* WARN */
@-webkit-keyframes warn {
    0% { background-color: hsla(216, 97%, 100%, 1) }
    100% { background-color: hsla(216, 97%, 96%, 1) }
}
@keyframes warn {
    0% { background-color: hsla(216, 97%, 100%, 1) }
    100% { background-color: hsla(216, 97%, 96%, 1) }
}
.warn {
    -webkit-animation: warn 1.5s infinite alternate;
            animation: warn 1.5s infinite alternate;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

/* SUN */
.sun--highlight {
    background: #eee;
}
.sun__legend {
    background: #f9f9f9;
}
.sun__legend-more {
    padding: 0px 20px 10px;
    font-size: 14px;
}



